}

.wrimagecard {
  margin-top: 0;
  margin-bottom: 1.5rem;
  text-align: left;
  position: relative;
  background: #fff;
  box-shadow: 12px 15px 20px 0px rgba(46, 61, 73, 0.15);
  border-radius: 4px;
  transition: all 0.3s ease;
}

.wrimagecard .fa {
  position: relative;
  font-size: 70px;
}

.wrimagecard-topimage_header {
  padding: 20px;
}

a.wrimagecard:hover,
.wrimagecard-topimage:hover {
  box-shadow: 2px 4px 8px 0px rgba(46, 61, 73, 0.2);
}

.wrimagecard-topimage a {
  width: 100%;
  height: 100%;
  display: block;
}

.wrimagecard-topimage_title {
  padding: 20px 24px;
  height: 80px;
  padding-bottom: 0.75rem;
  position: relative;
}

.wrimagecard-topimage a {
  border-bottom: none;
  text-decoration: none;
  color: #525c65;
  transition: color 0.3s ease;
}

.team-section {
  margin-top: 40px;
}

.team-member img {
  width: 100%;
  height: auto;
  /* border-radius: 50%; */
}

.team-member .card {
  border: none;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.team-member .card-body {
  padding: 20px;
}

.team-member .card-title {
  font-weight: 700;
}

.faq-icon {
  width: 14px;
  aspect-ratio: 0.58;
  object-fit: cover;
  object-position: center;
}

.faq-subheader {
  font-weight: 700;
  font-size: 48px;
  font-family: "Hanuman", sans-serif;
}

@media (max-width: 991px) {
  .faq-subheader {
    font-size: 40px;
    line-height: 51px;
  }
}

.faq-description {
  font-weight: 400;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
}

