/* HTML: <div class="loader"></div> */
.loader {
  width: 50px;
  aspect-ratio: 1;
  background-image: url("/public/logo.png");
  background-size: cover;
  background-position: center;
  animation: l3 1s infinite linear;
}
@keyframes l3 {
  to {
    transform: rotate(1turn);
  }
}


.active{
  font-weight: bold ;
}